function getAll(selector) {
  return Array.prototype.slice.call(document.querySelectorAll(selector), 0);
}

function responsivity() {
  var $dropdowns = getAll('.navbar-item.has-dropdown:not(.is-hoverable)');
  var $burgers = getAll('.burger');

  function closeDropdowns() {
    $dropdowns.forEach(function ($el) {
      $el.classList.remove('is-active');
    });
  }

  if ($dropdowns.length > 0) {
    $dropdowns.forEach(function ($el) {
      $el.addEventListener('click', function (event) {
        // eslint-disable-line no-unused-vars
        event.stopPropagation();
        $el.classList.toggle('is-active');
      });
    });

    document.addEventListener('click', function (event) {
      // eslint-disable-line no-unused-vars
      closeDropdowns();
    });
  }

  // Close dropdowns if ESC pressed
  document.addEventListener('keydown', function (event) {
    var e = event || window.event;
    if (e.keyCode === 27) {
      closeDropdowns();
    }
  });

  // Toggles
  if ($burgers.length > 0) {
    LG('--- We haz ' + $burgers.length + ' burgers. ---');
    LG($burgers);
    $burgers.forEach(function ($el) {
      $el.addEventListener('click', function () {
        var target = $el.dataset.target;

        var $target = document.getElementById(target);
        // LG('------');
        // LG($el.classList);
        // LG($target.classList);
        // LG('------');
        $el.classList.toggle('is-active');
        $target.classList.toggle('is-active');
      });
    });
  } else {
    LG('--- Yes! We haz no burgers ---');
  }
}

function shutBurgerMenu() {
  getAll('.burger').forEach(function (burger) {
    return burger.classList.remove('is-active');
  });
  getAll('.burger-bar').forEach(function (bar) {
    return bar.classList.remove('is-active');
  });
}

LG('        ############# Responsive menu event handling installed #############');

